        <el-button type="primary" @click="confirmBatchEdit" size="small">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 批量修改图片标签对话框 -->
    <el-dialog
      title="图片标签批量修改"
      :visible.sync="batchImgTagEditVisible"
      width="30%"
      class="imglistBatchOperation"
    >
      <el-form>
