<script>
import UploadImgList from '@/components/UploadImgList'
export default {
  data() {
    return {
      selectedId:[],
      sortby: 'update_at',
      sortorder: 'desc',
      gallery_name: '', //搜索关键词
      keepChecked: true,
      checkboxaccount: [0],
      AccountList: [],
      batchCategoryList: [
        {
          id: 1,
          pid: 0,
          type: '1',
          label: '植物',
        },
      ],
      propsCategory: {
        label: 'label',
        children: 'children',
      },
      batchOperationForm: {
        name: '',
        tag: '',
        category: '',
        mark: '',
      }, //批量操作表单
      batchImgCategoryEditVisible: false,
      batchImgTagEditVisible: false,
      batchImgNameEditVisible: false,
      disabledPre: false,
      disabledNext: false,
      operationName: '批量操作',
      operationSelect: 'name',
      total: 0,
      pageSize: this.$store.state.imgListComData.limit,
      logo:
        'http://kjds-img.img-cn-shanghai.aliyuncs.com/2018/11-23/112902796ABeo3FPqvJL1.jpg',
      selectValue: '', //选中值
      currentPage: 1,
      priImg: '', //数据源存放容器
      multipleSelection: [],
      loading: false, //数据加载状态
      //处理数据源容器
      multipleSelectionId: [],
      checkedimagesList: [], //已选中
      imageList: [], //上传图片容器
      //添加图片
      uploadFileConOne: {
        limit: 9999,
        uploadSrc:
          `http://api.321.design/v1/GalleryUploadImgs?u_id=${sessionStorage.getItem(
            'selectUId'
          )}&cate_id=${
            sessionStorage.getItem('imgListCateId') || '-1'
          }&type=${sessionStorage.getItem('imageType')}`,
        //CHANGE_IMGLIST_CATE_ID
        multiple: true,
        buttonState: false,
        imgList: [],
        comName: 'imgList',
      },
      //图片列表
      imagesList: [],
      // 图片路径列表
      imgsPathList: [],
      imgPathListLength: 0,
      // 双击时图片路径在路径列表里的索引
      imgIndex: 0,
      isIndeterminate: true, //
      centerDialogVisible: false, //图片预览对话行显示状态
      errorMessage: '', //提示信息
      type: '', //提示信息状态
      dialogFormVisible: false, //修改图片信息状态
      dialogImageVisible: false, //添加图片对话框状态
      /*提交form信息*/
      form: {},
      formLabelWidth: '80px', //formLabel的宽度
      titleName: '', //dialog标题名称

      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '全部',
            onClick(picker) {
              const end = '1'
              const start = '1'
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
      timeValue: [],
      timeValueUpdate: [],
      defaultProps: {
        children: 'children',
        label: 'label',
      },
      //弹出框下拉框数据
      options: [
        {
          id: 0,
          label: '根目录',
          children: [],
        },
      ],
      props: {
        value: 'id',
        children: 'children',
      },
      //树形控件数据
      treeData: [],
      //校验规则
      rules: {
        name: [{ required: true, message: '请输入目录名称', trigger: 'blur' }],
        cate_pid: [
          { required: true, message: '请选择所属上级', trigger: 'change' },
        ],
      },
    }
  },
  methods: {
    //重置 去掉所有搜索条件
    refresh() {
      this.timeValue = [] //清空时间
      this.timeValueUpdate = [] //清空时间
      this.gallery_name = '' //清空搜索框

      this.$store.state.imgListComData.timeValue = [] //清除创建时间查询
      this.$store.state.imgListComData.timeValueUpdate = [] //清除创建时间查询
      this.$store.state.imgListComData.gallery_name = '' //清除关键字查询
      this.initData()
    },

    conver(limit) {
      var size = ''
      if (limit < 0.1 * 1024) {
        //如果小于0.1KB转化成B
        size = limit.toFixed(2) + 'B'
      } else if (limit < 0.1 * 1024 * 1024) {
        //如果小于0.1MB转化成KB
        size = (limit / 1024).toFixed(2) + 'KB'
      } else if (limit < 0.1 * 1024 * 1024 * 1024) {
        //如果小于0.1GB转化成MB
        size = (limit / (1024 * 1024)).toFixed(2) + 'MB'
      } else {
        //其他转化成GB
        size = (limit / (1024 * 1024 * 1024)).toFixed(2) + 'GB'
      }
      var sizestr = size + ''
      var len = sizestr.indexOf('.')
      var dec = sizestr.substr(len + 1, 2)
      if (dec == '00') {
        //当小数点后为00时 去掉小数部分
        return sizestr.substring(0, len) + sizestr.substr(len + 3, 2)
      }
      return sizestr
    },
    sortby1(val) {
      this.sortby = val
      if (this.sortorder == 'desc') {
        this.sortorder = 'asc'
      } else {
        this.sortorder = 'desc'
      }

      this.initData()
    },
    searchtext() {
      // console.log(this.timeValue);
      this.$store.state.imgListComData.gallery_name = this.gallery_name
      this.$store.state.imgListComData.timeValue = this.timeValue //按创建时间查询
      this.$store.state.imgListComData.timeValueUpdate = this.timeValueUpdate //按创建时间查询
      //  this.$store.commit('CHANGE_IMGLIST_CATE_GALLRRYNAME', this.searchValue);
      this.initData()
    },
    //改变全选状态
    handleSelectionChange(val) {
      let arr = val.map(function(item, index, val) {
        return item.id;
      }, this);
      this.selectedId = arr
    },
    changeaccount(val) {
      if (val == 0) {
        this.checkboxaccount = [0]
      } else {
        if (this.checkboxaccount.indexOf(0) != -1) {
          this.checkboxaccount.splice(this.checkboxaccount.indexOf(0), 1)
        }
      }
      if (this.checkboxaccount.length == 0) {
        this.checkboxaccount = [0]
      }
      this.$refs.tree.setCurrentKey(sessionStorage.getItem('imgListCateId'))
      this.$store.state.orderManagement.merchants_user_id = this.checkboxaccount
      this.$store.state.changeUserId = this.checkboxaccount
      this.initData()
    },
    cancelBatchCategory() {
      this.batchImgCategoryEditVisible = false
      this.$refs.tree.setCheckedKeys([])
    },
    batchHandleCheckChange(data, checked, indeterminate) {},
    // 确定批量修改名字
    async confirmBatchEdit() {
      if (this.operationSelect == 'name') {
        let res = await this.api.GalleryEdit({
          id: this.selectedId,
          name: this.batchOperationForm.name,
        })
        if (res.data.code == 200) {
          this.$message({
            type: 'success',
            message: '名称批量修改成功',
          })
        } else {
          this.$message({
            type: 'warning',
            message: '名称批量修改失败',
          })
        }
        this.batchImgNameEditVisible = false
        this.batchOperationForm.name = ''
      } else if (this.operationSelect == 'tag') {
        let res = await this.api.GalleryEdit({
          id: this.selectedId,
          label: this.batchOperationForm.tag,
        })
        if (res.data.code == 200) {
          this.$message({
            type: 'success',
            message: '标签批量修改成功',
          })
        } else {
          this.$message({
            type: 'warning',
            message: '标签批量修改失败',
          })
        }
        this.batchImgTagEditVisible = false
        this.batchOperationForm.tag = ''
      } else if (this.operationSelect == 'category') {
        var cateId = this.$refs.tree.getCheckedKeys().join(',')
        let res = await this.api.GalleryCatesEdits({
          id: this.selectedId,
          cate_id: cateId,
        })
        if (res.data.code == 200) {
          this.$message({
            type: 'success',
            message: '分类批量修改成功',
          })
        } else {
          this.$message({
            type: 'warning',
            message: '分类批量修改失败',
          })
        }
        this.batchImgCategoryEditVisible = false
        this.$refs.tree.setCheckedKeys([])
      } else if (this.operationSelect == 'mark') {
      } else {
      }
      this.initData()
    },
    // 点击批量操作下拉菜单
    batchOperation(val) {
      if (this.selectedId.length == 0) {
         this.$message({
          message: '请选择商品',
          type: 'warning',
        })
        return
      }
      this.operationSelect = val
      if (val == 'name') {
        this.operationName = '批量设置图片名称'
        this.batchOperationForm.name = ''
        this.batchImgNameEditVisible = true
      } else if (val == 'tag') {
        this.operationName = '批量设置图片标签'
        this.batchOperationForm.tag = ''
        this.batchImgTagEditVisible = true
      } else if (val == 'mark') {
        this.operationName = '批量添加标记'
      } else if (val == 'category') {
        this.operationName = '批量设置图片分类'
        this.batchImgCategoryEditVisible = true
      }
    },
    changeStatus() {
      this.dialogImageVisible = false
    },
    //tree字段检索
    filterNode(value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
