    async transferreDdirectory(cate_id, goods_id) {
      var type = sessionStorage.getItem('imageType')
      let res = await this.api.GalleryChangeCate({
        cate_id: cate_id,
        gallery_id: goods_id,
        type: type,
      })
      if (res.data.data == '1') {
        this.initData()
        this.$message({
          message: '转移目录成功',
          type: 'success',
        })
      } else {
        this.$message.error('转移目录失败')
      }
    },
    //修改图片名称+图片标签
    async handleChange(fieldName, value, label, id, cateId) {
      var type = sessionStorage.getItem('imageType')
      if (label == undefined) {
        label = ''
      } else if (label == null) {
        label = ''
      }
      if (value == '') {
        this.$message({
          type: 'error',
          message: '名称不能为空',
        })
        return
      }
      let res = await this.api.GalleryEdit({
        id: id,
        name: value,
        label: label,
        type: type,
        cate_id: cateId,
      })
      if (res.data.data == '1') {
        this.$message({
          message: '修改成功',
          type: 'success',
        })
      } else {
        this.$message.error(res.data.msg)
      }
    },
