    clickTree(data) {
      this.handleId(this.checkedimagesList)
      let addShop = ''
      if (this.checkedimagesList.length > 0) {
        addShop = this.multipleSelectionId.join(',')
        this.transferreDdirectory(data.id, addShop)
      } else {
        this.$message({
          message: '请选择商品',
          type: 'warning',
        })
      }
    },
    //处理传入数据的id，循环遍历出来
    handleId(data) {
      this.multipleSelectionId = []
      for (const key in data) {
        if (data.hasOwnProperty(key)) {
          const element = data[key]
          this.multipleSelectionId.push(element.id)
        }
      }
    },
    //双击图片预览
    dbClickImg(imgSrc) {
      this.imgIndex = this.imgsPathList.indexOf(imgSrc)
      this.imgPathListLength = this.imgsPathList.length
      if (this.imgIndex == 0) {
        this.disabledPre = true
      } else {
        this.disabledPre = false
      }
      if (this.imgIndex == this.imgPathListLength - 1) {
        this.disabledNext = true
      } else {
        this.disabledNext = false
      }
      this.centerDialogVisible = true
      this.priImg = this.imgsPathList[this.imgIndex]
    },
    // 点击预览上一张
    clickPre() {
      this.disabledNext = false
      this.imgIndex--
      if (this.imgIndex == 0) {
        this.disabledPre = true
      } else {
        this.disabledPre = false
      }
      this.priImg = this.imgsPathList[this.imgIndex]
    },
    // 点击预览下一张
    clickNext() {
      this.disabledPre = false
      this.imgIndex++
      if (this.imgIndex == this.imgPathListLength - 1) {
        this.disabledNext = true
      } else {
        this.disabledNext = false
      }
      this.priImg = this.imgsPathList[this.imgIndex]
    },
