    //修改图片信息
    changeImg() {
      if (this.selectedId.length > 0) {
        this.dialogFormVisible = true
        this.titleName = '个人图库--修改'
        this.form = this.checkedimagesList[0]
        this.form.cate_pid = []
        if (this.checkedimagesList[0].cates_ids) {
          let pidList = this.checkedimagesList[0].cates_ids.split(',')
          for (let index = 0; index < pidList.length; index++) {
            const element = pidList[index]
            this.form.cate_pid.push(Number(element))
          }
          this.form.cate_pid.push(Number(this.checkedimagesList[0].id))
        } else {
          this.form.cate_pid.push(0)
        }
        if (this.checkedimagesList[0].status == '1') {
          this.form.status = true
        } else {
          this.form.status = false
        }
      }
      if (this.checkedimagesList.length == 0) {
        this.errorMessage = '请选择一条记录进行修改'
        this.type = 'warning'
        this.messageTips(this.errorMessage, this.type)
      }
      // if(this.checkedimagesList.length>1){
