    //删除图片信息
    async delImg() {
      if (this.checkedimagesList.length > 0) {
        let id = []
        for (let index = 0; index < this.checkedimagesList.length; index++) {
          const element = this.checkedimagesList[index]
          id.push(element.id)
        }
        this.loading = true
        var imageType = sessionStorage.getItem('imageType')
        var delId = id.join(',')
        let res = await this.api.GalleryDelete({
          id: delId,
          type: imageType,
        })
        if (res.data.data == '1') {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success',
          })
          this.initData()
          this.loading = false
        } else {
          this.$message.error(res.data.msg)
        }
      } else {
        this.$message({
          showClose: true,
          message: '请选择想要删除的图片',
          type: 'warning',
        })
      }
    },
