    //确认图片修改信息
    changeImgInfo(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.handleId(this.checkedimagesList)
          let fromData = {}
          fromData.cate_id = this.form.cate_pid[this.form.cate_pid.length - 1]
          fromData.name = this.form.name
          fromData.id = this.multipleSelectionId
          fromData.label = this.form.label
          fromData.status = this.form.status == '1' ? '1' : '2'
          fromData.type = sessionStorage.getItem('imageType')
          let res = await this.api.GalleryEdit(fromData)
          this.dialogFormVisible = false
          this.initData()
          if (res.data.data == '1') {
            this.$message({
              message: '修改成功',
              type: 'success',
            })
          } else {
            this.$message.error(res.data.msg)
          }
        } else {
          return false
        }
      })
    },
    //获取商品分类目录数据（用作转目录）
    async getTreeData(
      u_id = sessionStorage.getItem('selectUId'),
      type = sessionStorage.getItem('imageType')
    ) {
      this.treeData = []
      this.options[0].children = []
      let res = await this.api.GalleryCateList({
        type: type,
        merchants_user_id: this.checkboxaccount,
      })
      this.batchCategoryList = res.data.data.data
      for (const key in res.data.data.data) {
        if (res.data.data.data.hasOwnProperty(key)) {
          const element = res.data.data.data[key]
          this.options[0].children.push(element)
          this.treeData.push(element)
        }
      }
    },
