    //初始化数据
    async initData(
      u_id = sessionStorage.getItem('selectUId'),
      type = sessionStorage.getItem('imageType')
    ) {
      this.loading = true
      this.checkedimagesList = []
      this.$store.state.imgListComData.merchants_user_id = this.checkboxaccount
      this.$store.state.imgListComData.type = sessionStorage.getItem(
        'imageType'
