      this.$store.state.imgListComData.sortby = this.sortby
      // this.$store.state.imgListComData.limit = this.pageSize
      this.$store.state.imgListComData.sortorder = this.sortorder
      console.log(this.$store.state.imgListComData);
      let res = await this.api.gallery(this.$store.state.imgListComData)
      console.log(res.data.data.data)
      this.imagesList = res.data.data.data
      let arr = []
      for (var i = 0; i < this.imagesList.length; i++) {
        arr.push(this.imagesList[i].path)
      }
      this.imgsPathList = arr
      this.total = res.data.data.total
      if (res.data.data.data.length > 0) {
        sessionStorage.setItem('imgChildrenData', '1')
      } else {
        sessionStorage.setItem('imgChildrenData', '0')
      }
      this.loading = false
    },
    //选择节点111
    optionChange(value) {},
    formatDate(v) {
      if (/^(-)?\d{1,10}$/.test(v)) {
        v = v * 1000
      } else if (/^(-)?\d{1,13}$/.test(v)) {
        v = v * 1
      }
      var dateObj = new Date(v)
      var month = dateObj.getMonth() + 1
      var day = dateObj.getDate()
      var hours = dateObj.getHours()
      var minutes = dateObj.getMinutes()
      var seconds = dateObj.getSeconds()
      if (month < 10) {
        month = '0' + month
      }
      if (day < 10) {
        day = '0' + day
      }
      if (hours < 10) {
        hours = '0' + hours
      }
      if (minutes < 10) {
        minutes = '0' + minutes
      }
      if (seconds < 10) {
        seconds = '0' + seconds
      }
      var UnixTimeToDate =
        dateObj.getFullYear() +
        '-' +
        month +
        '-' +
        day +
        ' ' +
        hours +
        ':' +
        minutes +
        ':' +
        seconds
      return UnixTimeToDate
    },
  },
