/*图库图片分类目录*/
<template>
  <div class="MainCon">
    <div class="d-flex jc-center">
          <el-button size="mini" @click="clickAdd" style="background: #4B94CC;color: #fff;">
            <i class="el-icon-plus">添加</i>
          </el-button>
          <el-button type="warning" size="mini" @click="clickWrite">
            <i class="el-icon-edit">修改</i>
          </el-button>
          <el-button type="danger" size="mini" @click="clickReduce">
            <i class="el-icon-delete">删除</i>
          </el-button>
      </div>
    <div class="operationList">
      <div class="treeTitle d-flex fs-xl jc-start">
        <i class="iconfont icon_mulu fs-xl"></i>
        <div style="margin-left:.5em">图片分类目录</div>
      </div>
      <div class="tree tree1" v-loading="cateLoading">
        <el-tree
          class="filter-tree"
          :data="data"
          accordion
          :props="defaultProps"
          node-key="id"
          :filter-node-method="filterNode"
          @node-click="clickTree"
          :expand-on-click-node="false"
          ref="tree"
        ></el-tree>
      </div>
    </div>
    <el-dialog :title="titleName" :visible.sync="dialogFormVisible" center>
      <el-form ref="ruleForm" :rules="rules" :model="form" size="small">
        <el-form-item label="所属上级" :label-width="formLabelWidth" prop="cate_pid">
          <el-cascader
            change-on-select
            :options="options"
            v-model="form.cate_pid"
            :show-all-levels="false"
            :props="props"
            class="width100"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="目录名称" :label-width="formLabelWidth" prop="cate_name">
          <el-input v-model="form.cate_name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="排序" :label-width="formLabelWidth">
          <el-input-number v-model="sortNumber" :min="1" :max="99" label="描述文字"></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cencelAdd">取 消</el-button>
        <el-button size="small" type="primary" @click="confirmAdd('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
let id = 1000
export default {
  data() {
    return {
      cateLoading: false,
      count: 0, //共几笔
      filterText: '', //检索条件
      //tree数据
      data: [],
      operationStatus: '', //判断状态为添加还是修改
      //修改节点临时操作容器
      writeTree: {},
      defaultProps: {
        children: 'children',
        label: 'label',
      },
      sortNumber: 1,
      switchStatus: true, //添加目录开启状态滑块
      treeData: { id: -1, label: '未被分类' }, //操作节点临时容器
      dialogFormVisible: false, //dialog显示状态
      form: {}, //新增目录数据存储容器
      formLabelWidth: '80px',
      titleName: '', //dialog标题名称
      optionData: [],
      //校验规则
      rules: {
        cate_name: [
          { required: true, message: '请输入目录名称', trigger: 'blur' },
        ],
        cate_pid: [
          { required: true, message: '请选择所属上级', trigger: 'change' },
        ],
      },
      //弹出框下拉框数据
      options: [
        {
          id: 0,
          label: '根目录',
          children: [],
        },
      ],
      props: {
        value: 'id',
        children: 'children',
      },
    }
  },
  computed: {
    changeUserId() {
      return this.$store.state.changeUserId
    },
    ...mapGetters(['getImgListCateId']),
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val)
    },
    changeUserId: function (newVal, oldVal) {
      this.initData()
    },
  },

  methods: {
    //检索节点
    filterNode(value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    //节点点击
    clickTree(data) {
      this.$store.commit('CHANGE_IMGLIST_CATE_ID', data.id)
      this.writeTree = data
      // ;
    },
    //新增节点
    clickAdd() {
      this.titleName = '图库目录操作 -- 新增'
      this.dialogFormVisible = true
      this.operationStatus = 'addOperation'
      this.form.cate_pid = []
      if (this.writeTree.path) {
        let pidList = this.writeTree.path.split(',')
        for (let index = 0; index < pidList.length; index++) {
          const element = pidList[index]
          this.form.cate_pid.push(Number(element))
        }
        this.form.cate_pid.push(Number(this.writeTree.id))
      } else {
        this.form.cate_pid.push(0)
      }
    },
    //新增目录函数
    async addCatalog(data, u_id = sessionStorage.getItem('selectUId')) {
      var imageType = sessionStorage.getItem('imageType')
      let res = await this.api.GalleryCatesAdd(data)
      if (res.data.data == 1) {
        this.initData()
        this.$store.commit('IMG_TRANSFERRED_DIRECTORY_STATUS')
        this.dialogFormVisible = false
        this.$message({
          message: '新增目录成功',
          type: 'success',
        })
        this.form.cate_name = ''
      } else {
        this.$message.error('新增目录失败')
      }
    },
    //修改节点
    clickWrite() {
      this.handleClick(Event, this.writeTree)
    },
    //右键单击修改目录
    handleClick(event, data) {
      if (data.id == '-1' || data.id == '0') {
        return false
      } else {
        this.form = {}
        this.titleName = '图库目录操作 -- 修改'
        this.dialogFormVisible = true
        this.form.rank = data.rank
        this.sortNumber = data.rank
        this.$set(this.form, 'cate_name', data.label)
        this.form.cate_pid = []
        this.form.cate_id = data.id
        this.operationStatus = 'changeOperation'
        if (data.path) {
          let pidList = data.path.split(',')
          for (let index = 0; index < pidList.length; index++) {
            const element = pidList[index]
            this.form.cate_pid.push(Number(element))
          }
        } else {
          this.form.cate_pid.push(0)
        }
      }
    },
    //修改目录函数
    async changeCatalog(data) {
      let res = await this.api.GalleryCatesEdit(data)
      if (res.data.data == 1) {
        this.initData()
        this.$store.commit('IMG_TRANSFERRED_DIRECTORY_STATUS')
        this.dialogFormVisible = false
        this.$message({
          message: '修改目录成功',
          type: 'success',
        })
        this.form.cate_name = ''
      } else {
        this.$message.error('修改目录失败')
      }
    },
    //删除节点
    clickReduce() {
      let _id = sessionStorage.getItem('shopCate_id')
      let imgChildrenData = sessionStorage.getItem('imgChildrenData')
      // this.$store.commit('IMG_TRANSFERRED_DIRECTORY_STATUS')
      if (_id == '-1' || _id == '0') {
        return false
      } else {
        if (imgChildrenData == '1') {
          this.$message({
            message:
              '该目录下还有商品，不能删除！(如果当前没看到，可能在其他状态下有商品）',
            type: 'warning',
          })
        } else {
          this.deleteCatalog()
        }
      }
    },
    // 删除前确认
    deleteCatalog() {
      this.$confirm('此操作将永久删除该目录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.delFun()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
          })
        })
    },
    //删除商品分类目录
    async delFun() {
      var imageType = sessionStorage.getItem('imageType')
      var cate_id = sessionStorage.getItem('imgListCateId')
      let res = await this.api.GalleryCateDelete({
        cate_id: cate_id,
        type: imageType,
      })
      if (res.data.data == 1) {
        this.dialogFormVisible = false
        this.$store.commit('IMG_TRANSFERRED_DIRECTORY_STATUS')
        this.$store.commit('CHANGE_IMGLIST_CATE_ID', -1)
        this.$message({
          message: '删除目录成功',
          type: 'success',
        })
        this.initData()
      } else {
        this.$message.error('删除目录失败')
      }
    },
    //取消添加节点
    cencelAdd() {
      this.dialogFormVisible = false
    },
    //选择节点
    optionChange(value) {
      this.treeData = this.data.filter((item) => item.id === value)
      // ;
    },
    //确认添加节点
    confirmAdd(formName) {
      this.$refs[formName].validate((valid) => {
        let responseData = {}
        if (valid) {
          responseData.cate_pid = this.form.cate_pid[
            this.form.cate_pid.length - 1
          ]
          responseData.cate_name = this.form.cate_name
          responseData.rank = this.sortNumber
          responseData.status = this.switchStatus ? '1' : ''
          responseData.type = sessionStorage.getItem('imageType')
          if (this.operationStatus == '') {
            return false
          }
          if (this.operationStatus == 'addOperation') {
            this.addCatalog(responseData)
          }
          if (this.operationStatus == 'changeOperation') {
            responseData.cate_id = this.form.cate_id
            this.changeCatalog(responseData)
          }
        } else {
          this.$message({
            message: '请将信息填写完整',
            type: 'warning',
          })
          return false
        }
      })
    },
    async initData(type = sessionStorage.getItem('imageType')) {
      this.cateLoading = true
      let res = await this.api.GalleryCateListCount({
        type: type,
        merchants_user_id: this.$store.state.changeUserId,
      })
      if (res.data.code == '200') {
        this.data = []
        this.options[0].children = []
        this.count = res.data.data.count
        for (const key in res.data.data.data) {
          if (res.data.data.data.hasOwnProperty(key)) {
            const element = res.data.data.data[key]

            this.data.push(element)
            this.options[0].children.push(element)
          }
        }
        this.cateLoading = false
      } else {
        this.cateLoading = false
        this.errorMess('获取图片分类列表失败')
      }
    },
  },
  created() {
    this.initData()
    this.imageTType = sessionStorage.getItem('imageType')
  },
}
</script>

<style lang="scss" scoped>
.MainCon {
  border: 1px solid #dcdfe6;
  width: 210px;

  background-color: #fff;
  .treeTitle {
    padding-left: 1em;
    height: 2.5em;
    line-height: 2.5em;
    color: #333;
    background-color: #f9f9f9;
    border-bottom: 1px solid #dcdfe6;
  }
  .operationList {
    width: 100%;
    padding: 0;
    text-align: center;
    .tree {
      height: calc(100vh - 11em);
      overflow: auto;
    }
    i {
      font-size: 10px;
    }
    p {
      padding: 5px 0;
      font-size: 14px;
    }
  }
}

.iconSpan {
  display: inline-block;
  font-size: 12px !important;
}
</style>
