// 图库批量上传图片+进度条
<template>
  <div class="uploadFileCon">
    <div style="margin: 0px 0 10px;">
      <span>
        图片名称：
        <el-input placeholder="请输入图片名称" style="width: 50%!important;" v-model="imgName.name"></el-input>
      </span>
      <div style="float:right;">
        <el-button
          style="background: #4B91CC;color: #fff;"
          size="small"
          type="primary"
          @click="submitUpload"
        >上传</el-button>
        <el-button size="small" @click="cancelAdd">取消</el-button>
      </div>
    </div>

    <!-- <el-button style="margin: 10px;" size="small" type="success" @click="submitUpload" >上传到服务器</el-button> -->

    <el-upload
      class="upload-demo"
      ref="upload"
      drag
      :action="uploadFileData.uploadSrc"
      list-type="picture-card"
      :data="this.imgName"
      :multiple="uploadFileData.multiple"
      name="image"
      :auto-upload="false"
      :limit="uploadFileData.limit"
      :file-list="fileList"
      :on-exceed="handleExceed"
      :on-success="handleSuccess"
      :on-error="handleError"
      :before-upload="beforeUpload"
    >
      <!-- :action="uploadFileData.uploadSrc" -->
      <!-- :file-list="uploadFileData.imgList" -->
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">
        将文件拖到此处，或
        <em>点击上传</em>
      </div>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" v-lazy="dialogImageUrl" alt />
    </el-dialog>
  </div>
</template>
<script>
export default {
  props: ['uploadFileData'],
  data() {
    return {
      imgName: {
        name: '',
      },
      dialogImageUrl: '',
      dialogVisible: false,
      lengthNum: '',
      fileList: this.uploadFileData.imgList,
      timeList: [],
      successList: [],
      uploadStatus: [],
      // defaultUrl: 'http://api.321.design'
    }
  },
  methods: {
    // handlePictureCardPreview(file) {
    // this.dialogImageUrl = file.url;
    // this.dialogVisible = true;
    // },
    handleExceed() {
      this.$message({
        type: 'warning',
        message: `文件不能超过${this.uploadFileData.limit}哦~`,
      })
    },
    // 取消上传
    cancelAdd() {
      this.imgName.name = ''
      this.fileList = []
      this.uploadStatus = []
      this.$store.commit('IMG_LIST_UPLOAD_STATUS', false)
    },
    beforeUpload() {
      this.uploadStatus = []
    },
    //上传失败
    handleError(err, file, fileList) {
      this.uploadStatus.push('error')
      if (this.uploadStatus.length == fileList.length) {
        this.fileList = []
        this.$store.commit('IMG_LIST_UPLOAD_STATUS', false)
      }
    },
    // 上传成功
    handleSuccess(response, file, fileList) {
      this.imgName.name = ''
      // this.fileList = []
      this.uploadStatus.push('success')
      if (this.uploadStatus.length == fileList.length) {
        this.fileList = []
        this.$store.commit('IMG_LIST_UPLOAD_STATUS', false)
      }
    },
    //手动提交
    submitUpload: function () {
      //自定义的上传图片的方法
      // 1. 创建formData 利用AXIOS传递
      this.$refs.upload.submit() //调用submit方法
    },
  },
  created() {
    this.u_id = sessionStorage.getItem('selectUId')
  },
  computed: {
    cateId() {
      return this.$store.state.imgListComData.cate_id
    },
  },
}
</script>

<style lang="scss">
.uploadFileCon {
  .el-upload--picture-card {
    width: 100%;
  }
  .el-upload--picture-card {
    line-height: 3;
    width: 100%;
  }
  .el-upload-dragger {
    width: 100%;
  }
}
</style>